body {
    background-color: black;
    color: #ffffff;
    width: 100%;
    height: 100%;
}
/* 
.all-content {
    margin-top: 5%;
} */

.lat-text{
    margin: 2% 0 0 10%;
    font-size: 400%;
}

.long-text {
    margin: 0 0 2% 8.5%;
    font-size: 400%;
}

.main-image{
    width: 40%;
    float: left;
    margin: 2.8% 0 0 6%;
    position: absolute;
}

.name-container {
    margin-right: 15%;
}

.main-name{
    font-size: 600%;
    margin: 0 53% 10% 0;
    line-height: 1;
}


.main-content {
    float: right;
    margin: 5% 8% 0 0;
    width: 40%;
    border-radius: 10px;
    position: relative;
    z-index: 10;
}

.about-me {
    margin-right: 10%;
    text-align: center;
}

.main-body {
    margin: 0 auto;
}

.color-block {
    background-color: #7a1712;
    margin: 0% 0% 0% 9%;
    height: 112%;
    width: 40%;
    float: right;
    position: absolute;
    z-index: -5;
}

.contact-title {
    margin: 5% 0 5% 0;
    font-size: 350%;
}

.c-links{
    margin: 0 0 0 2%;
}

.c-btn{
    color: #ffffff;
    text-decoration: none;
}

.c-btn:hover {
    color: #7a1712;
    text-decoration: none;
}

.project-content {
    position: absolute;
    top: 155%;
    width: 100%;
}

.p-title {
    margin-left: 6%;
    font-size: 350%;
}

.bts-title {
    float: left;
    margin: 1% 0 0 6%;
    width: 25%;
    font-size: 500%;
    line-height: 90%;
    text-decoration: none;
    color: #ffffff;
    border: 2px solid #fff;
    padding: 15px;
}

.bts-title:hover {
    color:#7a1712;
}

.bts-content {
    margin: 0 6% 2% 0;
    width: 55%;
    float: right;
}

.brew-title {
    width: 28%;
    font-size: 500%;
    line-height: 90%;
    text-decoration: none;
    color: #ffffff;
    border: 2px solid #fff;
    padding: 15px;
    position: absolute;
    top: 46%;
    right: 64.5%;
}

.brew-title:hover {
    color:#7a1712;
}

.brew-content {
    margin: 0 6% 2% 6%;
    width: 55%;
    float: right;
}

.beat-title {
    width: 25%;
    font-size: 500%;
    line-height: 90%;
    text-decoration: none;
    color: #ffffff;
    border: 2px solid #fff;
    padding: 15px;
    position: absolute;
    top: 76%;
    right: 66.5%;
}

.beat-title:hover {
    color:#7a1712;
}

.beat-content {
    margin: 0 6% 2% 6%;
    width: 55%;
    float: right;
}

@media screen and (max-width: 1130px) {

    .color-block{
        height: 75%;
    }

   .bts-title {
    font-size: 350%;
    width: 20%;
   }

   .brew-title {
    font-size: 350%;
    width: 22%;  
    right: 68.5%;         
   }

   .beat-title {
    font-size: 350%;
    width: 15%;
    right: 72.5%;  
   }
}

@media screen and (max-width: 800px) {
    .main-name {
       font-size: 300%; 
    }
}


@media screen and (max-width: 500px) {

    .lat-text {
        font-size: 150%;
    }

    .long-text {
        font-size: 150%;
    }

    .main-image {
        margin-top: 8%;
        justify-content: center;
    }

    .color-block {
        margin-top: 5%;
        height: 40%;
        width: 40%;
    }

    .main-name {
        font-size: 200%;
        margin: 0% 0% 0% 20%; 
    }

    .bts-title {
        font-size: 200%;
        width: 25%;
    }

    .bts-content {
        margin: 0 5% 6% 0;
    }
    
    .brew-title {
        font-size: 200%;
        width: 27%;
        right: 63.5%;  
    }

    .brew-content {
        margin: 4% 5% 6% 0;
    }

    .beat-title {
        font-size: 250%;
        width: 23%;
        right: 67%;
    }

    .main-body {
        font-size: 80%;
        columns: none;
    }

    .project-content {
        position: absolute;
        top: 85%;
        width: 100%;
    }
  }